body{
    padding: 0;
    margin: 0;
}
.school-container {
    padding: 0;
    margin: 0;
  }
  .school-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
    background: linear-gradient(to right, #a8d8f0, #d1e9f2);
  }
  
  .logo-section {
    display: flex;
    align-items: center;
    gap: 10px;
    padding-left: 30px;
  }
  
  .school-logo {
    width: 50px;
    height: auto;
  }
  
  .school-name {
    font-size: 30px;
    font-weight: bold;
  }
  
  .navbar {
    display: flex;
    gap: 20px;
    margin-right: 300px;
    /* background: linear-gradient(to right, #a8d8f0, #d1e9f2); */
  }
  
  .nav-link {
    text-decoration: none;
    color: #333;
    font-size: 25px;
  }
  
  .nav-link:hover {
    color: #007bff;
  }
  